import React from 'react';
import YouTube from 'react-youtube';
import "./CssFeature.css"
import codeImage from "../../images/codeImage.png"
import BackLink from "../../components/BackLink/BackLink";
import TextParagraph from "../../components/TextParagraph/TextParagraph";

function CssFeature() {
    //YouTube video ID
    const videoId = 'q63tkui9YLQ';

    const opts = {
        height: '100%',
        width: '100%'
    };

    return (
        <div className="CssFeature-Wrapper">

            <BackLink/>

            <div className="CssFeature">

                <div className="CssFeature-VideoWrapper">
                    <h1 className={"CssFeature-Heading"}>Scroll Snap CSS</h1>
                    <YouTube videoId={videoId} opts={opts} className="CssFeature-Video"/>
                </div>

                <div className="CssFeature-Description">
                    <p className="CssFeature-Text">
                        <TextParagraph text={
                            "Scroll Snap         . " +
                            "     ,  ,    ."}/>
                    </p>
                    <img src={codeImage} className="CssFeature-Image" alt="codeImage"/>
                    <p className="CssFeature-Text">
                        <TextParagraph
                            text={"    <code>scroll-snap-type</code>  <code>scroll-snap-align</code>."}
                        />
                        <TextParagraph
                            text={" <code>scroll-snap-type</code>   -      ."}
                        />
                        <TextParagraph
                            text={"  <code>x</code>, <code>y</code>,    <code>block</code>  <code>inline</code> .      <code>both</code> ,     ."}
                        />
                        <TextParagraph
                            text={"    <code>mandatory</code>,  <code>proximity</code>,   ."}
                        />
                    </p>
                    <p className="CssFeature-Text">
                        <TextParagraph
                            text={" <code>scroll-snap-align</code>   <code>start</code>, <code>end</code>,  <code>center</code>, ,       ."}
                        />
                        <TextParagraph
                            text={" <code>scroll-padding</code>  <code>scroll-margin</code>       ."}
                        />
                    </p>
                    <p className="CssFeature-Text">
                        <TextParagraph
                            text={"  scroll snap       " +
                                " ,     ."}
                        />
                        <TextParagraph
                            text={'  Scroll Snap   <a href="https://developer.mozilla.org/ru/docs/Web/CSS/CSS_scroll_snap/Basic_concepts" '+
                                'target="_blank" '+
                                'rel="noreferrer" '+
                                'class="CssFeature-Link">  </a>'}
                        />
                    </p>
                </div>
            </div>
        </div>
    );
}

export default CssFeature;